import React,{Fragment} from "react";
import Loader from '../ajax-loader.gif';
import  BackBtn from './backBtn'

//  class components 

class User extends React.Component {
  componentDidMount() {
    this.props.getUser(this.props.match.params.login);
    console.log(this.props.user)
  }

  render() {
    const {loading} = this.props
    const {
        name,
        bio,
        blog,
        login,
        company,
        location,
      avatar_url,
      followers,
      following,
      html_url,
      hireable,
    } = this.props.user;

    return loading ? <img  className='loader' src={Loader} alt='loading... ' /> :   <div className='container-2'>
          <BackBtn />
    <div className="container-3">
          <div className= "col-right">
                 <img className="user-img" src={avatar_url} alt="user-img" />

                {hireable && <h2 className="hireable">
                   
                   </h2>}

                   <h2 className='name'>
                     {name}
                   </h2>
                   <h2 className='location'>
                       Location:  {location}
                   </h2>
          </div>
          <div className= "col-left">
               {bio && <Fragment>
                   <h1 className="bio-heading">
                     BIO
                   </h1>
                   <p className="bio">
                     {bio}
                   </p>
                   <a className="bio-btn" href={html_url}> Visit on github</a>
                   <ul>
                     <li>
                       {login && <Fragment>
                         Username: {login}
                         </Fragment>}
                     </li>
                     <li>
                       {company && <Fragment>
                         Company: {company}
                         </Fragment>}
                     </li>
                     <li>
                       {blog && <Fragment>
                         Website: {blog}
                         </Fragment>}
                     </li>
                   </ul>
                 </Fragment>}
          </div>
    </div>
    <div className="pill-container pills">
               <span className="pill success" ><strong>Followers:</strong>{followers}</span>
               <span className="pill danger"><strong>Following:</strong>{following}</span>
               <span className="pill info"><strong>hireable:</strong>{hireable ? <i className=" fa fa-check"></i>: <i className=" fa fa-times"></i>}</span>
    </div>
</div> }
}


