import React from 'react';
import {Link} from 'react-router-dom'

 const Navbar = () => {

    return <nav className= 'navbar'>
        
        <i className='logo fa fa-github'></i>
        <h2 className="menu">Github finder</h2>

        <ul>
            <li>
                <Link to='/' >Home</Link>
            </li>
            <li>
                <Link to='/About' >About</Link>
            </li>
        </ul>
     
    </nav>
}

export default Navbar

