// class app components

let client_id;
let client_secret;


if(process.env.NODE_ENV !==  'production') {
  client_id = process.env.REACT_APP_GITHUB_CLIENT_ID;
  client_secret = process.env.REACT_APP_GITHUB_CLIENT_SECRET
}  else {
  client_id = process.env.GITHUB_CLIENT_ID;
  client_secret = process.env.GITHUB_CLIENT_SECRET
}


const App = () => {
 
  const [inputText, setInputtext] = useState("");
    const [user, setUser] = useState({});
    const [users, setUsers] = useState([]);
    const [loading, setLoading] = useState(false);
    const [alert, setAlert] = useState(null)
  // async componentDidMount() {
  //   this.setState({
  //     loading: true,
  //   });

  //   const resp = await fetch("http://api.github.com/users");
  //   const data = await resp.json();
  //   this.setState({
  //     Users: data,
  //     loading: false,
  //   });
  // }


  const fecthUSers = async () => {
        setLoading(true);
        const resp = await fetch(`http://api.github.com/users?&client_id=${client_id}&client_secret=${client_secret}`);
        const data = await resp.json();
    
        setUsers(data);
        setLoading(false);
      };
      useEffect(() => {
        fecthUSers();
      }, []);
    


  const getUser = async (username)  => {

    // this.setState({
    //   loading: true,
    // });
  setLoading(true)
    const resp = await fetch(`http://api.github.com/users/${username}&client_id=${client_id}&client_secret=${client_secret}`);
    const data = await resp.json();
    // this.setState({
    //   User: data,
    //   loading: false,
    // });
  setUser(data)
  setLoading(false)
  }

  
 const onChange = (e) => {
    // this.setState({
    //   inputText: e.target.value,
    // });
    setInputtext(e.target.value)
  };

 const onSubmit = async (e) => {
    e.preventDefault();
    // this.setState({
    //   loading: true,
    // });
    if(inputText === '') {

       setAlert(true)

       setTimeout(()=> {
         setAlert(null)
       },3000)
      return false
    } {
      setLoading(true)
      const resp = await fetch(
        `https://api.github.com/search/users?q=${inputText}&client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client_secret=${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`
      );
      const data = await resp.json();
      // this.setState({
      //   Users: data.items,
      //   loading: false,
      //   inputText: "",
      // });
      setUsers(data.items)
      setLoading(false)
      setInputtext('')
    }
  
    
  };

  

  const onClick = () => {
  

    setUsers([])
  };


return    <div className="App">
      <Router>
        <Navbar />
   
        <Switch>
          <Route exact path="/" render= { props => {
          return  <Fragment>
            {alert?    <Alert  alert={{type:'warning',msg:'please type something'} } />: true}

             <Searchbox
            placeholder="Search for github users"
            onSubmit={onSubmit}
            onChange={onChange}
            value={inputText}
          />
                  <div className="users-container">
            {users.length > 0 && (
              <ClearBtn onClick={onClick} />
            )}
            {loading ? (
              <img className="loader" src={Loader} alt="loading..." />
            ) : (
          <Users users={users} />
            )}
          </div>
               </Fragment>
          }} />

          <Route exact path="/About" component={About} />
            {/* <About /> */}
      <Route exact path ='/user/:login'  render = {
        props =>{return <User  {...props} getUser={getUser} loading={loading} user={user}/>}
      }/>
        </Switch>
      </Router>
    </div>



}

