import React from 'react';
import {Link} from 'react-router-dom'

const Card = (props) => {
   return <div className="user">
        <img src={props.user.avatar_url}  />
        <h1>
           {props.user.login}
        </h1>
        <Link className="btn" to= {`user/${props.user.login}`}>read more</Link>
   </div>
}


export default Card


